
@font-face {
	font-family: 'baskerville';
	src: url('fonts/uoq.eot');
	src: url('fonts/uoq.eot?#iefix') format('embedded-opentype'),
		url('fonts/uoq.woff') format('woff'),
		url('fonts/uoq.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'baskerville';
	src: url('fonts/uoq_1.eot');
	src: url('fonts/uoq_1.eot?#iefix') format('embedded-opentype'),
		url('fonts/uoq_1.woff') format('woff'),
		url('fonts/uoq_1.ttf') format('truetype');
	font-weight: normal;
	font-style: italic;
}


@font-face {
  font-family: 'graphik';
  src: url('fonts/graphik-regular-webfont.eot'); /* IE9 Compat Modes */
  src: url('fonts/graphik-regular-webfont.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('fonts/graphik-regular-webfont.woff') format('woff'), /* Pretty Modern Browsers */
       url('fonts/graphik-regular-webfont.ttf')  format('truetype'), /* Safari, Android, iOS */
       url('fonts/graphik-regular-webfont.svg#svgFontName') format('svg'); /* Legacy iOS */
}

@font-face {
  font-family: 'graphik';
  src: url('fonts/graphik-regularitalic-webfont.eot'); /* IE9 Compat Modes */
  src: url('fonts/graphik-regularitalic-webfont.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('fonts/graphik-regularitalic-webfont.woff') format('woff'), /* Pretty Modern Browsers */
       url('fonts/graphik-regularitalic-webfont.ttf')  format('truetype'), /* Safari, Android, iOS */
       url('fonts/graphik-regularitalic-webfont.svg#svgFontName') format('svg'); /* Legacy iOS */
  font-style:italic;
}

@font-face {
  font-family: 'graphik';
  src: url('fonts/graphik-mediumitalic-webfont.eot'); /* IE9 Compat Modes */
  src: url('fonts/graphik-mediumitalic-webfont.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('fonts/graphik-mediumitalic-webfont.woff') format('woff'), /* Pretty Modern Browsers */
       url('fonts/graphik-mediumitalic-webfont.ttf')  format('truetype'), /* Safari, Android, iOS */
       url('fonts/graphik-mediumitalic-webfont.svg#svgFontName') format('svg'); /* Legacy iOS */
  font-style:italic;
  font-weight:bold;
}

@font-face {
  font-family: 'graphik';
  src: url('fonts/Graphik-Medium-Web.eot'); /* IE9 Compat Modes */
  src: url('fonts/Graphik-Medium-Web.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('fonts/Graphik-Medium-Web.woff') format('woff'), /* Pretty Modern Browsers */
       url('fonts/Graphik-Medium-Web.ttf')  format('truetype'), /* Safari, Android, iOS */
       url('fonts/Graphik-Medium-Web.svg#svgFontName') format('svg'); /* Legacy iOS */

  font-weight:bold;
}